import * as React from 'react'
import { Link } from 'gatsby'
import UI from '../components/UI/UI'

const NotFoundPage = () => {
  return (
    <UI title='404 Not Found'>
      <section className='s-about' data-sal='slide-left' data-sal-delay='100' data-sal-easing='ease-in-out'>
        <div className='row section-header add-bottom'>
          <div className='col-full'>
            <h1 className='display-1 display-1--light text-center'>404!</h1>
          </div>
        </div>
        <div className='row about-desc'>
          <div className='col-full'>
            <p>It seems that you're lost, the page you're looking for isn't here.</p>

            <Link to='/' title='Home' className='btn btn--stroke alt-black-btn-color'>
              Home
            </Link>
            <Link to='/projects' title='Projects' className='btn btn--stroke alt-black-btn-color'>
              All Projects
            </Link>
          </div>
        </div>
      </section>
    </UI>
  )
}

export default NotFoundPage
